.next-button {
    position: absolute;
    top: 150px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    z-index: 100;
}

.next-button-indicator {
    color: white;
    padding: 10px 15px;
    border-radius: 8%;
    background-color: #3f51b5;
    margin: 0 5px;
}
