.node {
    position: relative;
    height: 35px;
    width: 35px;
    background-color: #b2ebf2;
    border-radius: 100%;
}

.node .react-flow__handle {
    visibility: hidden;
}

.node.visited {
    background-color: #a5d6a7;
}

.node.destination {
    border: 2px dashed black;
}

.node.source {
    border: 2px solid black;
}

.node .label {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-size: 18px;
    line-height: 18px;
}

.node .shortest-path {
    position: absolute;
    bottom: -15px;
    left: 30px;
    font-size: 15px;
    font-weight: bold;
    color: #a5d6a7;
}
